body {
    background-color: slategray;
}

.header {
    text-align: center;
}

.logo {
    width: 600px;
    height: 300px;
}

.body {
    display: flex;
    align-items: center;
    justify-content: center;
}

#campeones {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 10px 10px;
    width: 100%;
}

.button {
    color: white;
    background-color: #d5b868;
    background: rgb(213,184,104);
    background: linear-gradient(160deg, rgb(212, 171, 56) 0%, rgb(240, 198, 143) 50%, rgba(212, 171, 56) 100%); 
    border: none;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    font-family: 'DejaVu Sans Mono';
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0,2), 0 6px 20px 0 rgba(0, 0, 0, 0, 19);
    border-radius: 8px;
}

.card {
    background-color: black;
    border: 2px #d5b868 solid;
    border-radius: 20px;
    text-align: center;
}

.card .splash{
    width: 100%;
    border-radius: 20px;
}

.card .info, .card .icon{
    display: none;
    color: white;
    font-size: 18px;
}

.card:hover .splash {
    animation-name: splash;
    animation-duration: 2s;
    opacity: 65%;
    height: fit-content;
    z-index: 0;
}

.card:hover .info, .card:hover .icon {
    display: inline-block;
}

@keyframes splash {
    0% {
        opacity: 100%;
    }
    100% {
        opacity: 65%;
    }
}